<template>
  <div class="back-button">
    <button @click="goBack">⬅ Go Back</button>
  </div>
</template>

<script>
export default {
  name: 'GoBack',
  methods: {
    goBack() {
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped>
button {
  background-color: #42b983;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  transition: 0.2s ease;
}
button:hover {
  background-color: #2a936c;
}
.back-button {
  margin-top: 20px;
}
</style>